	module.exports = {"data":{"markdownRemark":{"html":"<style>\n.contents {\n  margin-left: 1.45rem;\n  margin-right: 1.45rem;\n  border-radius: 0.3em;\n  width: 60%;\n}\n</style>\n<div class=\"background contents\" markdown=\"1\">\n<table>\n<thead>\n<tr>\n<th>You can receive payments on HackerOne for finding and reporting valid vulnerabilities in the form of:</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><a href=\"#bounties\">Bounties</a></td>\n</tr>\n<tr>\n<td><a href=\"#swag\">Swag</a></td>\n</tr>\n<tr>\n<td><a href=\"#bonuses\">Bonuses</a></td>\n</tr>\n</tbody>\n</table>\n</div>\n<h3 id=\"bounties\">Bounties</h3>\n<p>A bounty is money you get rewarded with for reported and resolved bugs. They're used to attract the best hackers and to keep them incentivized to hack their programs. Bounties are used to encourage you to focus on particular assets by altering the reward amount for different vulnerability types. You won't get rewarded for every report you send in, but only for useful, valid reports.</p>\n<p>After a program has decided to award you a bounty and the bounty has been awarded, you'll receive an email to claim the bounty. HackerOne will ask to collect your tax form before processing the payout.</p>\n<h4>Bounty Splitting</h4>\n<p>HackerOne enables you to split bounties with other hackers that helped you find the vulnerability. This allows all hackers to receive contributions and awards for their efforts.</p>\n<p>To split a bounty with collaborating hackers:</p>\n<ol>\n<li>Navigate to the report you'd like to split the bounty with in your HackerOne Inbox.</li>\n<li>Select <b>Add collaborator</b>.</li>\n<li>Enter the email or username of the collaborating hacker.</li>\n<li>\n<p>Enter the ratio in how you want to split the bounty under <b>Bounty weight</b>.</p>\n<ul>\n<li>Bounties are split using this equation: (Total Bounty/Total Bounty Weight)x Hacker Bounty Weight</li>\n<li>In the example below, the calculation would be as follows:<br>snappyboy: ($1000/3)x1 = $333.33<br>jared@hooli.corp: ($1000/3)x2 = $666.67</br>\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-06be2.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.35573122529644%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"bounty_split\"\n        title=\"\"\n        src=\"/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-2f183.png\"\n        srcset=\"/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-67829.png 125w,\n/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-5c59f.png 250w,\n/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-2f183.png 500w,\n/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-c93e8.png 750w,\n/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-4e628.png 1000w,\n/static/bounty_split-d8cd07a9b7bf9bf1054d3d54c49a92c7-06be2.png 1012w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n</ul>\n</li>\n<li>Click <b>Invite</b>.</li>\n</ol>\n<p>The calculated bounty amounts will show for each hacker. When the bounty is awarded, the bounty will be split according to the weights assigned. Additionally, all collaborators can now view and comment on the report.</p>\n<p>Keep in mind that:</p>\n<ul>\n<li>Bounties can't be split retroactively (i.e. after the bounty has already been awarded).</li>\n<li>You can only add up to 10 collaborators per report.</li>\n</ul>\n<blockquote>\n<p><i><b>Note:</b> Bounty splitting is only enabled for programs that use the <a href=\"/programs/billing.html\">prepayment</a> billing option.</i></p>\n</blockquote>\n<h4>Donating Bounties to Charity</h4>\n<p>HackerOne enables you to donate your bounty directly to charity.</p>\n<p>If you want to donate a bounty:</p>\n<ol>\n<li>Sign in to your HackerOne account and <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">submit a support ticket</a>.</li>\n<li>\n<p>Mention the following in your request:</p>\n<ul>\n<li>The ID of the report you want to donate the bounty of. You can get the report ID from the URL (e.g. <a href=\"https://hackerone.com/reports/123\">https://hackerone.com/reports/123</a>, where 123 is the ID).</li>\n<li>The name and website of the charity you want to donate to.</li>\n<li>Whether or not you want to be named as the person donating or prefer to remain anonymous. Keep in mind that we can only attribute the donation to you if the charity allows us to give a name.</li>\n</ul>\n</li>\n</ol>\n<p>Upon receiving your request, HackerOne will do a quick check to see if a donation can be made to your selected charity. If for some reason a donation can't be made, HackerOne will notify you so an alternative can be made.</p>\n<p>Generally, if the charity accepts donations in USD through PayPal or Credit Card, HackerOne should be able to fulfill your request quickly. If the charity provides an electronic receipt, HackerOne will forward this to you as proof of payment.</p>\n<p>Keep in mind that you still need to have a valid tax form on file in order to donate your bounty. At this time, we can only donate bounties in full as we can't do partial or split donations.</p>\n<h3 id=\"swag\">Swag</h3>\n<p>Your program can elect to award you with various swag in addition to or instead of bounties. Swag includes merchandise as well as free coupons or vouchers for the services or products the program offers. Some programs also offer to host hackers near their office or cover admissions fees to conferences you're invited to attend.</p>\n<p>The program is responsible for fulfillment and delivery of swag to you.</p>\n<h3 id=\"bonuses\">Bonuses</h3>\n<p>Programs can award bonuses to recognize hackers for positive actions beyond finding valid vulnerabilities. Bonuses enable programs to offer more flexible incentives without increasing the market rate for bounties.</p>","frontmatter":{"title":"Payments","id":"hackers/payments","path":"/hackers/payments.html","description":null}}},"pathContext":{}}

